import React,{useState,useEffect,useCallback} from 'react'
import axios from 'axios';
import jsSHA from 'jssha';
import Nopig from '../../pic/nopic.jpg';
const HitoFood = () => {
    const [data,setData] = useState(null)
    const [city, setCity] = useState('Taipei');
    const [input,setInput] = useState(0);
    const [actInput,setActInput] = useState(0);
    const [act,setAct] = useState("");
    const URL = `https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot/${city}?%24top=6&%24skip=${input}&%24format=JSON`;
    const largeURL = `https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot/${city}?%24filter=Class1%20eq%20'${act}'%20or%20Class2%20eq%20'${act}'%20or%20Class3%20eq%20'${act}'&%24top=30&%24skip=${actInput}&%24format=JSON`;
                     
    

    //

    const search = async (url) => {
        try {
          const response = await axios
          ({
             method: "get",
             url: url,
             headers: getAuthorizationHeader(),
           })
        //   console.log(response.data);
          let parseData = await response.data;
          if(url === largeURL){
            setActInput(actInput + 30);
          }
          console.log(parseData);
          setData(response.data);
        }
        catch (error) {
          console.error(error);
        };
      } 

          //
    function getAuthorizationHeader() {
        const AppID = "5f26e8783f97436e95dd2706ae3e476c"
        const AppKey = "KckLmAGtZVRJyvvMA7aQoFg4XP0"
        const GMTString = new Date().toGMTString()
        const ShaObj = new jsSHA('SHA-1', 'TEXT')
      
        ShaObj.setHMACKey(AppKey, 'TEXT')
        ShaObj.update('x-date: ' + GMTString)
      
        const HMAC = ShaObj.getHMAC('B64')
      
        let Authorization = `hmac username="${AppID}", algorithm="hmac-sha1", headers="x-date", signature="${HMAC}"`
      
        return { Authorization: Authorization, 'X-Date': GMTString }
      };


           //  useEffect
        useEffect(() =>{
            console.log('');
            search(URL);
        },[]);

    useEffect(() =>{
        console.log('');
        if(act === ""){
            search(URL);
        }else{
            search(largeURL);
        }
    },[city,act,input]);

  

    const handleChange = (e) =>{
        // console.log(e.target.value);
        setCity(e.target.value);
        setInput(0)
        setActInput(0)
        // search();
    }

    const handleAct = (e) =>{
        console.log(e.target.value);
        setAct(e.target.value);
        setActInput(0)
        setInput(0)
    }

    const handleMore = (e) =>{
        // console.log(e);
        const inputIn = parseInt(input,10)
        setInput(inputIn + 6);
        const actInputIn = parseInt(actInput,10)
        setActInput(actInputIn + 30)
    }


    return (
    <div className="container">
        <div className='travalTitle'>
            <div className="travalSelect">
                <div className="travalSelectLeft">
                    <h1></h1>
                </div>
                <div className="travalSelectRightt">
                    <select onChange={handleChange} name="" id="areaSelect">
                        <option value=""></option>
                        <option value="Taipei"></option>
                        <option value="NewTaipei"></option>
                        <option value="Taoyuan"></option>
                        <option value="Taichung"></option>
                        <option value="Tainan"></option>
                        <option value="Kaohsiung"></option>
                        <option value="Keelung"></option>
                        <option value="Hsinchu"></option>
                        <option value="HsinchuCounty"></option>
                        <option value="MiaoliCounty"></option>
                        <option value="ChanghuaCounty"></option>
                        <option value="NantouCounty"></option>
                        <option value="YunlinCounty"></option>
                        <option value="ChiayiCounty"></option>
                        <option value="Chiayi"></option>
                        <option value="PingtungCounty"></option>
                        <option value="YilanCounty"></option>
                        <option value="HualienCounty"></option>
                        <option value="TaitungCounty"></option>
                        <option value="KinmenCounty"></option>
                        <option value="PenghuCounty"></option>
                        <option value="LienchiangCounty"></option>
                    </select>
                    <select name="" onChange={handleAct}>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                    </select>
                </div>                
            </div>
            <div className="travalSelectMain">
                <div className="travalSelectMainLeft">
    
                </div>
                <div className="travalSelectMainRight">
                {data && data.map((e) => {
                    // console.log(e.Picture.PictureUrl1);
                    let Picture = e?.Picture?.PictureUrl1
                    // console.log(Picture);
                    if(Picture === undefined){
                        Picture = Nopig;
                     }
                   return <div key={e.ScenicSpotID} className="topViewIn2">
                   <a href="##"><img src={Picture} alt=""/></a>
                   <h3 className="toggleColor">{e.ScenicSpotName}</h3>
                   <h4>{e.City}</h4>
                   </div>
               }
               )}
                </div>
                <div className="travalBtn d-none">
                    <button onClick={handleMore} href="/food"></button>
                </div>
            </div>  
        </div>
    </div>      
    )
}

export default HitoFood
